<script>
export default {
  data () {
    return {
      activeIndex: '',
      tabPosition: 'top',
      project_id: '',
      sysName: 'X',
      collapsed: false,
      sysUserName: '',
      sysUserAvatar: ''
    }
  },
  watch: {
    '$route.path' () {
      if (this.$route.matched.length > 1) {
        return this.$route.matched[0].path
      } else {
        return this.$route.path
      }
    }
  },
  computed: {
    // 关于v-for v-if，data里未定义的变量遍历使用计算属性。否则使用v-for v-if配合使用
    routes: function () {
      return this.$router.options.routes.filter(function (item) {
        return !item.projectHidden
      })
    }
  },
  methods: {
    handleselect: function (a, b) {

    },
    onSubmit () {
      console.log('submit!')
    },
    // 退出登录
    logout: function () {
      let _this = this
      this.$confirm('确认退出吗?', '提示', {
        // type: 'warning'
      }).then(() => {
        sessionStorage.removeItem('token')
        _this.$router.push('/login')
      }).catch(() => {

      })
    },
    showMenu (i, status) {
      console.log('sdsddddcfffff')
      this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-' + i)[0].style.display = status ? 'block' : 'none'
    }
  },
  mounted () {
    let user = sessionStorage.getItem('name')
    if (user) {
      // eslint-disable-next-line no-global-assign
      name = JSON.parse(user)
      console.log(name, 'namenamenamename')
      this.sysUserName = name || ''
      // this.sysUserAvatar = '../assets/user.png';
    }
    this.project_id = this.$route.params.project_id
  }
}

